import { Component, OnInit } from '@angular/core';
import {Sort} from '@angular/material';
import {Customer} from '../../../../models/Customer';


@Component({
  selector: 'ic-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss']
})
export class CustomersComponent implements OnInit {
  ngOnInit(): void {

  }

  customers: Customer[] = [
    {id: '13030008', fullName: "Nguyễn Thị Cẩm Loan", birthday: new Date()},
    {id: '13030008', fullName: "Nguyễn Thị Cẩm Loan", birthday: new Date()},
    {id: '13030008', fullName: "Nguyễn Thị Cẩm Loan", birthday: new Date()},
    {id: '13030008', fullName: "Nguyễn Thị Cẩm Loan", birthday: new Date()},
    {id: '13030008', fullName: "Nguyễn Thị Cẩm Loan", birthday: new Date()},
  ];

  sortedData: Customer[];

  columns = ['avatar','id','fullName', 'birthday','actions'];

  constructor() {
    this.sortedData = this.customers.slice();
  }

  sortData(sort: Sort) {
    const data = this.customers.slice();
    if (!sort.active || sort.direction === '') {
      this.sortedData = data;
      return;
    }

    this.sortedData = data.sort((a, b) => {
      const isAsc = sort.direction === 'asc';
      switch (sort.active) {
        case 'id': return compare(a.id.toUpperCase(), b.id.toUpperCase(), isAsc);
        case 'fullName': return compare(a.fullName.toUpperCase(), b.fullName.toUpperCase(), isAsc);
        default: return 0;
      }
    });
  }
}

function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
